import React from 'react'
import './top_menu_bar.scss'

export default function top_menu_bar() {
  return (
    <div className="top-bar">
      <div className="left">
        <h2>.the_town_fit</h2>
      </div>
      <div className="right">
        <div className="inner">
          <span id='top-line'/>
          <span id='middle-line'/>
          <span id='bottom-line'/>
        </div>
      </div>
    </div>
  )
}
